<template>
  <div
      :style="{
      width: '100%',
      maxWidth: '1000px',
      padding: '20px',
      boxShadow: '0px 4px 10px 0px rgba(0,0,0,0.302)',
      margin: '10px auto',
      position: 'relative',
      background: '#fff',
      borderRadius: '8px',
      boxSizing: 'border-box'
    }"
  >
    <el-form
        class="add-update-preview"
        ref="ruleForm"
        :model="ruleForm"
        :rules="rules"
        label-width="120px"
    >
      <!--  -->
      <!--
      <el-form-item
          :style="{
            width: '100%',
            maxWidth: '800px',
            padding: '10px',
            margin: '0 auto 10px',
            display: 'block',
            boxSizing: 'border-box'
          }"
          label=""
          prop="yuyuedanhao"
      >
        <el-input
            v-model="ruleForm.yuyuedanhao"
            placeholder=""
            readonly
        ></el-input>
      </el-form-item>
      -->
      <el-form-item
          :style="{
          width: '100%',
          maxWidth: '800px',
          padding: '10px',
          margin: '0 auto 10px',
          display: 'block',
          boxSizing: 'border-box'
        }"
          label=""
          prop="mingcheng"
      >
        <el-input
            v-model="ruleForm.mingcheng"
            placeholder=""
            clearable
            :readonly="ro.mingcheng"
        ></el-input>
      </el-form-item>
      <el-form-item
          :style="{
          width: '100%',
          maxWidth: '800px',
          padding: '10px',
          margin: '0 auto 10px',
          display: 'block',
          boxSizing: 'border-box'
        }"
          label=""
          prop="zuowei"
      >
        <el-input
            v-model="ruleForm.zuowei"
            placeholder=""
            clearable
            readonly
        ></el-input>
      </el-form-item>
      <!-- picker-options+ -->
      <el-form-item
          :style="{
        width: '100%',
          maxWidth: '800px',
          padding: '10px',
          margin: '0 auto 10px',
          display: 'block',
          boxSizing: 'border-box'
        }"
          label=""
          prop="timeRange"
      >
        <div style="display: flex; gap: 10px; align-items: center;">
          <!-- picker-options +  -->
          <el-date-picker
              v-model="selectedDate"
              type="date"
              placeholder=""
              style="width: 40%;"
              :picker-options="datePickerOptions"
              @change="generateTimeOptions"
              popper-class="custom-date-picker"
          ></el-date-picker>
          <!-- no-data-text -->
          <el-select
              v-model="ruleForm.yuyue_start"
              placeholder=""
              style="width: 30%;"
              @change="handleStartTimeChange"
              filterable
              no-data-text=""
          >
            <el-option
                v-for="time in availableStartTimeOptions"
                :key="time.value"
                :label="time.label"
                :value="time.value"
            ></el-option>
          </el-select>
          <!-- no-data-text -->
          <el-select
              v-model="ruleForm.yuyue_end"
              placeholder=""
              style="width: 30%;"
              @change="checkTimeRange"
              filterable
              no-data-text=""
          >
            <el-option
                v-for="time in availableEndTimeOptions"
                :key="time.value"
                :label="time.label"
                :value="time.value"
            ></el-option>
          </el-select>
        </div>
      </el-form-item>
      <el-form-item
          :style="{
          width: '100%',
          maxWidth: '800px',
          padding: '10px',
          margin: '0 auto 10px',
          display: 'block',
          boxSizing: 'border-box'
        }"
          label=""
          prop="beizhu"
      >
        <el-input
            v-model="ruleForm.beizhu"
            placeholder=""
            clearable
        ></el-input>
      </el-form-item>
      <el-form-item
          :style="{
          width: '100%',
          maxWidth: '800px',
          padding: '10px',
          margin: '0 auto 10px',
          display: 'block',
          boxSizing: 'border-box'
        }"
          label=""
          prop="xuehao"
      >
        <el-input
            v-model="ruleForm.xuehao"
            placeholder=""
            clearable
        ></el-input>
      </el-form-item>
      <el-form-item
          :style="{
    width: '100%',
          maxWidth: '800px',
          padding: '10px',
          margin: '0 auto 10px',
          display: 'block',
          boxSizing: 'border-box'
        }"
          label=""
          prop="xingming"
      >
        <el-input
            v-model="ruleForm.xingming"
            placeholder=""
            clearable
        ></el-input>
      </el-form-item>
      <el-form-item
          :style="{
          width: '100%',
          maxWidth: '800px',
          padding: '10px',
    margin: '0 auto 10px',
          display: 'block',
          boxSizing: 'border-box'
        }"
          label=""
          prop="shouji"
      >
        <el-input
            v-model="ruleForm.shouji"
            placeholder=""
            clearable
        ></el-input>
      </el-form-item>
      <el-form-item :style="{
        padding: '20px 0 0',
        margin: '0',
        textAlign: 'center',
        width: '100%'
      }">
        <el-button
            :style="{
    margin: '0 10px 10px',
            padding: '0 24px',
            height: '40px',
            borderRadius: '4px',
            fontSize: '14px',
            background: '#2e61e1',
            color: '#fff',
            border: 'none',
            transition: 'opacity 0.2s'
          }"
            type="primary"
            @click="onSubmit"
        ></el-button
        >
        <el-button
            :style="{
    margin: '0 10px 10px',
            padding: '0 24px',
            height: '40px',
            borderRadius: '4px',
            fontSize: '14px',
            background: '#f5f5f5',
            color: '#666',
            border: '1px solid #e4e7ed',
            transition: 'opacity 0.2s'
          }"
            @click="back()"
        ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      id: "",
      baseUrl: "",
      ro: {
        zixishiid: false,
        yuyuedanhao: false,
        mingcheng: false,
        zuowei: false,
        yuyue_start: false,
        yuyue_end: false,
        beizhu: false,
        xuehao: false,
        xingming: false,
        shouji: false,
        sfsh: false,
        shhf: false,
      },
      type: "",
      userTableName: localStorage.getItem("UserTableName"),
      ruleForm: {
        zixishiid: "",
        yuyuedanhao: this.getUUID(),
        mingcheng: "",
        zuowei: "",
        yuyue_start: "",
        yuyue_end: "",
        beizhu: "",
        xuehao: "",
        xingming: "",
        shouji: "",
      },
      selectedDate: "", // 
      availableStartTimeOptions: [], // 
      availableEndTimeOptions: [], // 
      // 
      TIME_CONFIG: {
        startHour: 8,    // 
        endHour: 22,     // 
        stepMinutes: 30  // 
      },
      // +
      datePickerOptions: {
        disabledDate: (time) => {
          const today = new Date();
          const todayTime = new Date(today.getFullYear(), today.getMonth(), today.getDate()).getTime();
          const tomorrowTime = new Date(today.getFullYear(), today.getMonth(), today.getDate() + 1).getTime();
          const currentTime = new Date(time.getFullYear(), time.getMonth(), time.getDate()).getTime();
          // 
          return currentTime !== todayTime && currentTime !== tomorrowTime;
        }
      },
      rules: {
        yuyuedanhao: [],
        mingcheng: [
          { required: true, message: "/", trigger: "blur" }
        ],
        zuowei: [],
        yuyue_start: [
          { required: true, message: "", trigger: "change" },
        ],
        yuyue_end: [
          { required: true, message: "", trigger: "change" },
        ],
        beizhu: [],
        xuehao: [
          { required: true, message: "", trigger: "blur" }
        ],
        xingming: [
          { required: true, message: "", trigger: "blur" }
        ],
        shouji: [{ validator: this.$validate.isMobile, trigger: "blur" }],
      },
    };
  },
  created() {
    let type = this.$route.query.type ? this.$route.query.type : "";
    this.init(type);
    this.baseUrl = this.$config.baseUrl;
    // 
    const today = new Date();
    this.selectedDate = this.formatDate(today);
    this.generateTimeOptions();
  },
  methods: {
    getMakeZero(s) {
      return s < 10 ? "0" + s : s;
    },
    download(file) {
      window.open(`${file}`);
    },
    //  yyyy-MM-dd
    formatDate(date) {
      const year = date.getFullYear();
      const month = this.getMakeZero(date.getMonth() + 1);
      const day = this.getMakeZero(date.getDate());
      return `${year}-${month}-${day}`;
    },
    //  selectedDate  yyyy-MM-dd
    getSelectedDateStr() {
      if (!this.selectedDate) return "";
      if (typeof this.selectedDate === "string") {
        return this.selectedDate.split(" ")[0];
      }
      return this.formatDate(this.selectedDate);
    },
    // Date
    parseDateTimeString(dateTimeStr) {
      if (!dateTimeStr || typeof dateTimeStr !== 'string') return null;
      const parts = dateTimeStr.trim().split(' ');
      if (parts.length < 2) return null;
      const datePart = parts[0];
      const timePart = parts[1];
      const [y, m, d] = datePart.split('-').map(Number);
      const [hh, mm, ss] = timePart.split(':').map(Number);
      if (![y, m, d].every(n => !isNaN(n))) return null;
      return new Date(y, (m || 1) - 1, d || 1, hh || 0, mm || 0, ss || 0);
    },
    // 30
    generateTimeOptions() {
      const selectedDateStr = this.getSelectedDateStr();
      if (!selectedDateStr) {
        this.availableStartTimeOptions = [];
        this.availableEndTimeOptions = [];
        this.ruleForm.yuyue_start = "";
        this.ruleForm.yuyue_end = "";
        return;
      }
      const { startHour, endHour, stepMinutes } = this.TIME_CONFIG;
      const now = new Date();
      const isToday = this.formatDate(now) === selectedDateStr;
      // 8:00 - 22:0030
      const timeList = [];
      for (let hour = startHour; hour <= endHour; hour++) {
        for (let minute = 0; minute < 60; minute += stepMinutes) {
          if (hour === endHour && minute > 0) break;
          const timeStr = `${this.getMakeZero(hour)}:${this.getMakeZero(minute)}:00`;
          const fullTimeStr = `${selectedDateStr} ${timeStr}`;
          const timeObj = this.parseDateTimeString(fullTimeStr);
          if (!timeObj) continue;
          // 
          if (isToday && timeObj.getTime() <= now.getTime()) continue;
          timeList.push({
            label: timeStr,
            value: fullTimeStr
          });
        }
      }
      this.availableStartTimeOptions = [...timeList];
      this.availableEndTimeOptions = [...timeList];
      this.ruleForm.yuyue_start = "";
      this.ruleForm.yuyue_end = "";
    },
    // 
    handleStartTimeChange() {
      if (!this.ruleForm.yuyue_start) {
        this.availableEndTimeOptions = [...this.availableStartTimeOptions];
        this.ruleForm.yuyue_end = "";
        return;
      }
      const startDateObj = this.parseDateTimeString(this.ruleForm.yuyue_start);
      if (!startDateObj) {
        this.$message.error("");
        this.availableEndTimeOptions = [];
        this.ruleForm.yuyue_end = "";
        return;
      }
      const startTimeTs = startDateObj.getTime();
      this.availableEndTimeOptions = this.availableStartTimeOptions.filter(item => {
        const t = this.parseDateTimeString(item.value);
        return t && t.getTime() > startTimeTs;
      });
      this.ruleForm.yuyue_end = "";
      if (this.availableEndTimeOptions.length === 0) {
        this.$message.warning("");
      }
    },
    // 
    checkTimeRange() {
      if (!this.ruleForm.yuyue_start || !this.ruleForm.yuyue_end) return;
      const startDateObj = this.parseDateTimeString(this.ruleForm.yuyue_start);
      const endDateObj = this.parseDateTimeString(this.ruleForm.yuyue_end);
      if (!startDateObj || !endDateObj) {
        this.$message.error("");
        return;
      }
      const start = startDateObj.getTime();
      const end = endDateObj.getTime();
      if (end <= start) {
        this.$message.error("");
        this.ruleForm.yuyue_end = "";
      } else {
        const startDay = this.formatDate(startDateObj);
        const endDay = this.formatDate(endDateObj);
        if (startDay !== endDay) {
          this.$message.error("");
          this.ruleForm.yuyue_end = "";
        }
      }
    },
    init(type) {
      this.type = type;
      if (type == "cross") {
        var obj = JSON.parse(localStorage.getItem("crossObj") || "{}");
        // ====== seatSelection=======
        try {
          const seatSelection = localStorage.getItem('seatSelection');
          if (seatSelection) {
            const selectionObj = JSON.parse(atob(seatSelection));
            if (selectionObj.data && selectionObj.data.seatNumbers) {
              this.ruleForm.zuowei = selectionObj.data.seatNumbers.join(', ');
              this.ro.zuowei = true;
            }
          }
        } catch (e) {
          this.ruleForm.zuowei = '';
        }
        // 
        for (var o in obj) {
          if (o == 'id') {
            this.ruleForm.zixishiid = obj[o];
            this.ro.zixishiid = true;
            this.$http.get('zixishi/detail/' + obj[o]).then(res => {
              if (res.data.code == 0) {
                this.ruleForm.mingcheng = res.data.data.mingcheng;
                this.ro.mingcheng = true;
              }
            }).catch(err => {
              console.error("", err);
              this.$message.warning("");
            });
            continue;
          }
          if (o == "yuyuedanhao") {
            this.ruleForm.yuyuedanhao = obj[o];
            this.ro.yuyuedanhao = true;
            continue;
          }
          if (o == "mingcheng") {
            this.ruleForm.mingcheng = obj[o];
            this.ro.mingcheng = true;
            continue;
          }
          // seatSelectioncrossObjseatSelection
          if (o == "zuowei" && !this.ruleForm.zuowei) {
            this.ruleForm.zuowei = obj[o];
            this.ro.zuowei = true;
            continue;
          }
          if (o == "beizhu") {
            this.ruleForm.beizhu = obj[o];
            this.ro.beizhu = true;
            continue;
          }
          if (o == "xuehao") {
            this.ruleForm.xuehao = obj[o];
            this.ro.xuehao = true;
            continue;
          }
          if (o == "xingming") {
            this.ruleForm.xingming = obj[o];
            this.ro.xingming = true;
            continue;
          }
          if (o == "shouji") {
            this.ruleForm.shouji = obj[o];
            this.ro.shouji = true;
            continue;
          }
        }
      }
      // 
      this.$http
          .get(this.userTableName + "/session", { emulateJSON: true })
          .then((res) => {
            if (res.data.code == 0) {
              var json = res.data.data || {};
              if (json.xuehao || json.xuehao === 0) {
                this.ruleForm.xuehao = json.xuehao;
              }
              if (json.xingming || json.xingming === 0) {
                this.ruleForm.xingming = json.xingming;
              }
              if (json.shouji || json.shouji === 0) {
                this.ruleForm.shouji = json.shouji;
              }
            }
          })
          .catch(err => {
            console.error("", err);
          });
    },
    onSubmit() {
      // 1. 
      if (!this.ruleForm.yuyue_start) {
        this.$message.error("");
        return;
      }
      if (!this.ruleForm.yuyue_end) {
        this.$message.error("");
        return;
      }
      if (!this.ruleForm.xuehao) {
        this.$message.error("");
        return;
      }
      if (!this.ruleForm.xingming) {
        this.$message.error("");
        return;
      }
      if (!this.ruleForm.mingcheng) {
        this.$message.error("/");
        return;
      }
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          const submitData = {
            ...this.ruleForm,
            yuyueStart: this.ruleForm.yuyue_start,
            yuyueEnd: this.ruleForm.yuyue_end,
            yuyue_start: this.ruleForm.yuyue_start,
            yuyue_end: this.ruleForm.yuyue_end
          };
          console.log("", submitData);
          this.$http.post("yuyuexinxi/submit", submitData, {
            headers: {
              'Content-Type': 'application/json;charset=UTF-8'
            },
            emulateJSON: false
          })
              .then((res) => {
                console.log("", res.data);
                if (res.data.code === 0 || res.data.msg === "") {
                  this.$message({
                    message: "",
                    type: "success",
                    duration: 1500,
                    onClose: () => {
                      this.$router.go(-1);
                    },
                  });
                } else {
                  this.$message.error(res.data.msg || "");
                }
              })
              .catch(err => {
                console.error("", err);
                this.$message.error("" + (err.message || ""));
              });
        }
      });
    },
    getUUID() {
      return new Date().getTime();
    },
    back() {
      this.$router.go(-1);
    },
  }
};
</script>
<!-- scopedstyle -->
<style lang="scss">
.custom-date-picker {
  .el-date-table td.disabled {
    pointer-events: none !important; /*  */
    .cell {
      background-color: #f5f5f5 !important; /*  */
      color: #c0c4cc !important; /*  */
      text-decoration: line-through !important; /*  */
      text-decoration-color: #999 !important;
    }
  }
}
</style>
<style rel="stylesheet/scss" lang="scss" scoped>
/*  */
@media (max-width: 768px) {
  .add-update-preview .el-form-item >>> .el-form-item__label {
    width: 100% !important;
    text-align: left !important;
    line-height: 24px !important;
    padding: 0 0 8px 0 !important;
  }
  .add-update-preview .el-form-item >>> .el-form-item__content {
    margin-left: 0 !important;
  }
  .el-form-item .el-form-item__content > div {
    flex-direction: column;
  }
  .el-form-item .el-form-item__content > div .el-date-picker,
  .el-form-item .el-form-item__content > div .el-select {
    width: 100% !important;
    margin-bottom: 10px;
  }
}
.el-date-editor.el-input,
.el-select {
  width: 100%;
}
.add-update-preview .el-form-item >>> .el-form-item__label {
  padding: 0 10px 0 0;
  color: #666;
  font-weight: 500;
  width: 120px;
  font-size: 14px;
  line-height: 40px;
  text-align: right;
}
.add-update-preview .el-form-item >>> .el-form-item__content {
  margin-left: 120px;
}
.add-update-preview .el-input >>> .el-input__inner,
.add-update-preview .el-select >>> .el-input__inner,
.add-update-preview .el-date-editor >>> .el-input__inner {
  border: 2px solid #2e61e1;
  border-radius: 4px;
  padding: 0 12px;
  outline: none;
  color: #000;
  background: #bfd2fc;
  width: 100%;
  font-size: 14px;
  height: 40px;
  box-sizing: border-box;
}
::v-deep .el-select-dropdown__item {
  padding: 8px 16px;
  font-size: 14px;
}
::v-deep .el-select-dropdown__item.selected {
  color: #2e61e1;
  background-color: #f0f5ff;
}
::v-deep .el-button:hover {
  opacity: 0.9;
}
.add-update-preview .el-textarea >>> .el-textarea__inner {
  border: 2px solid #2e61e1;
  border-radius: 4px;
  padding: 12px;
  outline: none;
  color: #000;
  background: #bfd2fc;
  width: 100%;
  font-size: 14px;
  height: 120px;
}
</style>