<template>
  <div
      :style="{
      width: '100%',
      maxWidth: '1000px',
      padding: '20px',
      margin: '10px auto',
      position: 'relative',
      background: '#fff',
      boxSizing: 'border-box'
    }"
  >
    <!--  -->
    <div :style="{
      marginBottom: '20px',
      paddingBottom: '10px',
      borderBottom: '1px solid #e4e7ed'
    }">
      <el-breadcrumb :separator="'/'" :style='{"fontSize":"14px","lineHeight":"1","color":"#333"}'>
        <el-breadcrumb-item @click="$router.push('/index/home')"></el-breadcrumb-item>
        <el-breadcrumb-item @click="$router.push('/index/tousu')"></el-breadcrumb-item>
        <el-breadcrumb-item>{{ type === 'edit' ? '' : '' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <el-form
        class="add-update-preview"
        ref="ruleForm"
        :model="ruleForm"
        :rules="rules"
        label-width="120px"
        :style="{width: '100%', maxWidth: '800px', margin: '0 auto'}"
    >
      <!--  -->
      <el-form-item
          label=""
          prop="xuehao"
          :style="{marginBottom: '15px'}"
      >
        <el-input
            v-model="ruleForm.xuehao"
            placeholder=""
            clearable
            :readonly="ro.xuehao"
            :style="{
              width: '100%',
              height: '36px',
              border: '1px solid #dcdfe6',
              borderRadius: '4px',
              background: '#f5f7fa'
            }"
        ></el-input>
      </el-form-item>

      <!--  -->
      <el-form-item
          label=""
          prop="zixishi"
          :style="{marginBottom: '15px'}"
      >
        <el-radio-group
            v-model="ruleForm.zixishi"
            style="width: 100%;"
        >
          <el-radio
              v-for="(item, index) in zixishiOptions"
              :key="index"
              :label="item"
              :style="{
                display: 'inline-block',
                margin: '0 20px 10px 0',
                fontSize: '14px',
                color: '#666'
              }"
          >
            {{ item }}
          </el-radio>
        </el-radio-group>
      </el-form-item>

      <!--  -->
      <el-form-item
          label=""
          prop="zuoweihao"
          :style="{marginBottom: '15px'}"
      >
        <el-input
            v-model="ruleForm.zuoweihao"
            placeholder=""
            clearable
            :style="{
              width: '100%',
              height: '36px',
              border: '1px solid #dcdfe6',
              borderRadius: '4px'
            }"
        ></el-input>
      </el-form-item>

      <!--  -->
      <el-form-item
          label=""
          prop="tousuleixing"
          :style="{marginBottom: '15px'}"
      >
        <el-radio-group
            v-model="ruleForm.tousuleixing"
            style="width: 100%;"
        >
          <el-radio
              v-for="(item, index) in tousuleixingOptions"
              :key="index"
              :label="item"
              :style="{
                display: 'inline-block',
                margin: '0 20px 0 0',
                fontSize: '14px',
                color: '#666'
              }"
          >
            {{ item }}
          </el-radio>
        </el-radio-group>
      </el-form-item>

      <!--  -->
      <el-form-item
          label=""
          prop="tousuReason"
          :style="{marginBottom: '15px'}"
      >
        <el-input
            v-model="ruleForm.tousuReason"
            type="textarea"
            placeholder="10"
            clearable
            :rows="5"
            :style="{
              width: '100%',
              border: '1px solid #dcdfe6',
              borderRadius: '4px'
            }"
        ></el-input>
      </el-form-item>


      <!-- / -->
      <el-form-item :style="{textAlign: 'left', marginBottom: '0'}">
        <el-button
            :loading="submitting"
            type="primary"
            @click="onSubmit"
            :style="{
              padding: '0 20px',
              height: '36px',
              borderRadius: '4px',
              fontSize: '14px',
              background: '#409eff',
              color: '#fff',
              border: 'none',
              marginRight: '10px'
            }"
        >{{ type === 'edit' ? '' : '' }}</el-button
        >
        <el-button
            @click="back()"
            :style="{
              padding: '0 20px',
              height: '36px',
              borderRadius: '4px',
              fontSize: '14px',
              background: '#fff',
              color: '#666',
              border: '1px solid #dcdfe6'
            }"
        ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      id: "",
      baseUrl: "",
      submitting: false,
      // 
      ro: {
        xuehao: false,
      },
      type: "",
      userTableName: localStorage.getItem("UserTableName"),
      // 
      ruleForm: {
        tousudanhao: this.getUUID(), // 
        xuehao: "", // 
        zixishi: "", // 
        zuoweihao: "", // 
        tousuleixing: "", // 
        tousuReason: "", // 
        tousuTime: "", // 
        chulizhuangtai: "", //  status 
      },
      // 8
      zixishiOptions: ["", "s2s", "", "2", "", "", "", ""],
      // 
      tousuleixingOptions: ["", "", "", ""],
      // 
      rules: {
        // 
        xuehao: [
          { required: true, message: "", trigger: "blur" }
        ],
        // 
        zixishi: [
          { required: true, message: "", trigger: "change" }
        ],
        // 
        zuoweihao: [],
        // 
        tousuleixing: [
          { required: true, message: "", trigger: "change" }
        ],
        // 
        tousuReason: [
          { required: true, message: "", trigger: "blur" },
          { min: 10, message: "10", trigger: "blur" }
        ]
      },
    };
  },
  created() {
    let type = this.$route.query.type ? this.$route.query.type : "";
    this.init(type);
    this.baseUrl = this.$config?.baseUrl || '';
  },
  methods: {
    init(type) {
      this.type = type;
      if (type === "edit") {
        let editObj = JSON.parse(localStorage.getItem("editComplaintObj") || "{}");
        if (Object.keys(editObj).length > 0) {
          this.ruleForm = {
            ...this.ruleForm,
            ...editObj,
            tousuleixing: editObj.tousuleixing || "",
            zixishi: editObj.zixishi || ""
          };
        } else {
          this.$message.warning('');
        }
      }

      // 
      if (!this.userTableName) {
        this.ruleForm.xuehao = "20230001";
        this.ro.xuehao = true;
        return;
      }

      if (this.$http) {
        this.$http
            .get(this.userTableName + "/session", { emulateJSON: true })
            .then((res) => {
              if (res.data && (res.data.code === 0 || res.data.success === true)) {
                const userInfo = res.data.data || {};
                if (userInfo.xuehao) {
                  this.ruleForm.xuehao = userInfo.xuehao;
                  this.ro.xuehao = true;
                }
              }
            })
            .catch(err => {
              console.warn("", err);
              this.$message.warning("");
            });
      }
    },

    // 
    getUUID() {
      return "TS" + new Date().getTime();
    },

    //  ComplaintEntity 
    async onSubmit() {
      this.ruleForm.tousuTime = new Date().toISOString().slice(0, 19).replace('T', ' ');
      this.$refs["ruleForm"].validate(async (valid) => {
        if (!valid) return;
        if (this.submitting) return;
        this.submitting = true;

        try {
          // 
          // - complaintNo -> 
          // - userId -> 
          // - roomId ->  zixishiOptions  index+1  roomId id 
          // - seatId ->  0
          // - category ->  index+1
          // - detail -> 
          const userIdNum = (() => {
            const n = Number(this.ruleForm.xuehao);
            return Number.isNaN(n) ? this.ruleForm.xuehao : n;
          })();

          const roomIndex = this.zixishiOptions.indexOf(this.ruleForm.zixishi);
          const roomId = roomIndex >= 0 ? (roomIndex + 1) : 0;

          const seatIdNum = (() => {
            const n = Number(this.ruleForm.zuoweihao);
            return Number.isNaN(n) ? 0 : n;
          })();

          const categoryIndex = this.tousuleixingOptions.indexOf(this.ruleForm.tousuleixing);
          const category = categoryIndex >= 0 ? (categoryIndex + 1) : 0;

          const payload = {
            complaintNo: this.ruleForm.tousudanhao,
            userId: userIdNum,
            roomId: roomId,
            seatId: seatIdNum,
            category: category,
            evidenceUrls: null, //  URL  JSON 
            detail: this.ruleForm.tousuReason,
            // status / isProcessed 
          };

          console.log("", payload);

// add.vue 
          const headers = { 'Content-Type': 'application/json;charset=UTF-8' };
          const token = localStorage.getItem('Token') || localStorage.getItem('token') || localStorage.getItem('Authorization');
          if (token) {
            headers['Authorization'] = token.startsWith('Bearer ') ? token : `Bearer ${token}`;
            headers['Token'] = token;
          }

          const resp = await axios.post('/complaint/save', payload, {
            headers,
            withCredentials: true
          });
          // 
          if (resp && resp.data && resp.data.code === 0) {
            this.$message({
              message: this.type === 'edit' ? "" : "",
              type: "success",
              duration: 1600,
              onClose: () => {
                this.$router.push({
                  path: '/index/tousu',
                  replace: true
                });
              },
            });
          } else {
            //  R.ok  code
            const msg = (resp && resp.data && resp.data.msg) || '';
            this.$message.error(msg);
          }
        } catch (err) {
          console.error("", err);
          this.$message.error("");
        } finally {
          this.submitting = false;
        }
      });
    },

    // 
    back() {
      this.$router.push({
        path: '/index/tousu',
        replace: true
      });
    },
  },
};
</script>

<style lang="scss" scoped>
// 
@media (max-width: 768px) {
  .add-update-preview {
    ::v-deep .el-form-item__label {
      width: 100% !important;
      text-align: left !important;
      line-height: 24px !important;
      padding: 0 0 8px 0 !important;
    }

    ::v-deep .el-form-item__content {
      margin-left: 0 !important;
    }
  }
}

// 
.add-update-preview {
  ::v-deep .el-form-item__label {
    padding: 0;
    color: #666;
    font-weight: 400;
    width: 120px;
    font-size: 14px;
    line-height: 36px;
    text-align: left;
  }

  ::v-deep .el-form-item__content {
    margin-left: 0;
  }

  // 
  ::v-deep .el-radio__inner {
    width: 16px;
    height: 16px;
    border-radius: 50%;
  }

  // 
  ::v-deep .el-breadcrumb {
    .el-breadcrumb__separator {
      margin: 0 8px;
      color: #666;
    }

    .el-breadcrumb__inner {
      color: #333;
      cursor: pointer;
    }
  }
}
</style>