<template>
  <div :style='{"width":"80%","margin":"10px auto","position":"relative","background":"#fff","boxShadow":"0px 4px 10px 0px rgba(0,0,0,0.302)"}'>
    <!--  -->
    <div class="title" :style='{"margin":"0","color":"#333","textAlign":"center","background":"rgba(0,0,0,0.102)","fontSize":"20px","lineHeight":"54px","fontWeight":"600"}'>{{ title }}</div>

    <div :style='{"display":"flex","padding":"20px"}'>
      <div :style='{"width":"180px","textAlign":"center","background":"#f5f7fa","borderRadius":"8px","padding":"20px","marginRight":"20px"}'>
        <div :style='{"marginBottom":"15px","cursor":"pointer"}' @click="avatarClickTip">
          <img
              v-if="sessionForm.touxiang"
              :src="sessionForm.touxiang"
              :style='{"width":"100px","height":"100px","borderRadius":"50%","border":"2px solid #fff","boxShadow":"0 2px 8px rgba(0,0,0,0.1)"}'
          />
          <div
              v-else
              :style='{"width":"100px","height":"100px","borderRadius":"50%","background":"#e6f0ff","color":"#409eff","fontSize":"36px","lineHeight":"100px","display":"inline-block","textAlign":"center"}'>
            <i class="el-icon-user"></i>
          </div>

          <!--  el-upload <input> file input -->
          <el-upload
              ref="avatarUploader"
              class="avatar-uploader"
              :action="uploadUrl"
              :headers="headers"
              :show-file-list="false"
              :auto-upload="false"
              style="display: none;">
          </el-upload>
        </div>

        <div :style='{"fontSize":"14px","color":"#333","padding":"8px 0","background":"#fff","borderRadius":"4px","marginBottom":"20px"}'>
          {{ sessionForm.xuehao || '' }}
        </div>

        <div :style='{"display":"flex","flexDirection":"column","gap":"10px"}'>
          <el-button type="primary" plain @click="switchTab('')" :style="tabStyle('')"></el-button>
          <el-button type="primary" plain @click="switchTab('')" :style="tabStyle('')"></el-button>
          <el-button type="primary" plain @click="switchTab('')" :style="tabStyle('')"></el-button>
          <el-button type="primary" plain @click="switchTab('')" :style="tabStyle('')"></el-button>
        </div>
      </div>

      <div :style='{"flex":"1","background":"#f5f7fa","borderRadius":"8px","padding":"20px"}'>
        <div v-if="activeTab === ''" :style='{"background":"#fff","borderRadius":"8px","padding":"20px","boxShadow":"0 2px 8px rgba(0,0,0,0.05)"}'>
          <el-form ref="sessionForm" :model="sessionForm" :rules="rules" label-width="80px" :style='{"width":"100%","maxWidth":"500px","margin":"0 auto"}'>
            <el-form-item label="" prop="xingming" :style='{"marginBottom":"20px"}'>
              <el-input v-model="sessionForm.xingming" placeholder="" style="width: 300px;"></el-input>
            </el-form-item>
            <el-form-item label="" :style='{"marginBottom":"20px"}'>
              <el-select v-model="sessionForm.xingbie" placeholder="" style="width: 300px;">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="shouji" :style='{"marginBottom":"20px"}'>
              <el-input v-model="sessionForm.shouji" placeholder="" style="width: 300px;"></el-input>
            </el-form-item>
            <el-form-item label="" prop="banji" :style='{"marginBottom":"20px"}'>
              <el-input v-model="sessionForm.banji" placeholder="" style="width: 300px;"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit('sessionForm')" :style='{"background":"#409EFF","color":"#fff","border":"none","padding":"0 20px","height":"40px"}'></el-button>
            </el-form-item>
          </el-form>
        </div>

        <div v-if="activeTab === ''" :style='{"background":"#fff","borderRadius":"8px","padding":"30px 40px","textAlign":"center","boxShadow":"0 2px 8px rgba(0,0,0,0.05)"}'>
          <p :style='{"fontSize":"16px","color":"#666","marginBottom":"30px"}'></p>
          <!--  -->
          <el-button type="primary" @click="triggerAvatarUpload" class="upload-btn"></el-button>
          <div v-if="uploading" style="margin-top:12px;color:#999"></div>
        </div>

        <div v-if="activeTab === ''" class="pwd-card">
          <el-form ref="pwdForm" :model="pwdForm" :rules="pwdRules" label-width="120px" class="pwd-form">
            <el-form-item label="" prop="oldPassword">
              <el-input v-model="pwdForm.oldPassword" type="password" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="newPassword">
              <el-input v-model="pwdForm.newPassword" type="password" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="confirmPassword">
              <el-input v-model="pwdForm.confirmPassword" type="password" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" class="pwd-submit" @click="updatePassword"></el-button>
            </el-form-item>
          </el-form>
        </div>

        <div v-if="activeTab === ''" :style='{"width":"100%"}'>
          <div :style='{"fontSize":"18px","color":"#333","fontWeight":"500","marginBottom":"20px","paddingBottom":"10px","borderBottom":"1px solid #eee"}'></div>
          <div :style='{"background":"#fff","borderRadius":"8px","padding":"20px","boxShadow":"0 2px 8px rgba(0,0,0,0.05)","marginBottom":"20px"}'>
            <el-form :style="{ width: '100%', display: 'flex', alignItems: 'center', gap: '15px' }" :inline="true" :model="searchForm">
              <div :style="{ display: 'flex', flexDirection: 'column', flex: 1 }">
                <label :style="{ color: '#333', fontSize: '14px', fontWeight: '500', marginBottom: '4px' }"></label>
                <el-input v-model="searchForm.yuyuedanhao" placeholder="" clearable :style="{ width: '100%', borderColor: '#78abc3' }"></el-input>
              </div>
              <el-button type="success" @click="searchCancelOrder" :style='{"border":"1px solid #97C9D6","color":"#fff","background":"#4fc08d","borderRadius":"20px","height":"40px","padding":"0 18px"}'></el-button>
            </el-form>
          </div>

          <div :style='{"background":"#fff","borderRadius":"8px","boxShadow":"0 2px 8px rgba(0,0,0,0.05)","overflow":"hidden"}'>
            <el-table class="custom-table" :style="{ width: '100%' }" :data="cancelOrderList" v-loading="cancelOrderLoading" border :header-cell-style="{ background: '#f8f9fa', color: '#495057', fontWeight: '600' }" :row-style="{ height: '45px' }">
              <el-table-column type="index" label="" width="80" align="center"></el-table-column>
              <el-table-column prop="yuyuedanhao" label="" min-width="150" align="center"></el-table-column>
              <el-table-column prop="mingcheng" label="" min-width="120" align="center"></el-table-column>
              <el-table-column prop="yuyueStart" label="" min-width="180" align="center"></el-table-column>
              <el-table-column prop="yuyueEnd" label="" min-width="180" align="center"></el-table-column>
              <el-table-column prop="quxiaoshijian" label="" min-width="180" align="center"></el-table-column>
              <el-table-column prop="xuehao" label="" min-width="120" align="center"></el-table-column>
              <el-table-column prop="xingming" label="" min-width="120" align="center"></el-table-column>
              <el-table-column prop="shouji" label="" min-width="150" align="center"></el-table-column>
            </el-table>
          </div>

          <el-pagination @size-change="sizeChangeHandle" @current-change="currentChangeHandle" :current-page="pageIndex" background :page-sizes="[10,20,30,50]" :page-size="pageSize" :layout="'total, prev, pager, next, sizes, jumper'" :total="totalPage" :style="{ width: '100%', margin: '20px 0 0', textAlign: 'right' }"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from '@/config/config'

export default {
  data() {
    const validateMobile = (rule, value, callback) => {
      if (!value) {
        callback();
      } else {
        const mobileReg = /^1[3-9]\d{9}$/;
        if (!mobileReg.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      }
    };

    const vm = this;

    return {
      title: '',
      activeTab: '',
      baseUrl: config.baseUrl,
      sessionForm: {},
      pwdForm: { oldPassword: '', newPassword: '', confirmPassword: '' },
      rules: { xingming: [{ required: true, message: '', trigger: 'blur' }], shouji: [{ validator: validateMobile, trigger: 'blur' }] },
      pwdRules: {
        oldPassword: [{ required: true, message: '', trigger: 'blur' }],
        newPassword: [{ required: true, message: '', trigger: 'blur' }, { min: 6, message: '6', trigger: 'blur' }],
        confirmPassword: [{ required: true, message: '', trigger: 'blur' },
          { validator: function(rule, value, callback) { if (value !== vm.pwdForm.newPassword) callback(new Error('')); else callback(); }, trigger: 'blur' }]
      },
      uploadUrl: config.baseUrl + 'file/upload',
      headers: { Token: localStorage.getItem('Token') },
      userTableName: localStorage.getItem('UserTableName'),
      searchForm: { yuyuedanhao: '' },
      cancelOrderList: [], cancelOrderLoading: false, pageIndex: 1, pageSize: 10, totalPage: 0,
      uploading: false
    }
  },
  created() {
    this.getSession();
  },
  methods: {
    tabStyle(name) {
      return { width: "100%", background: this.activeTab === name ? "#409EFF" : "#fff", color: this.activeTab === name ? "#fff" : "#409EFF", borderColor: this.activeTab === name ? "#409EFF" : "#dcdfe6", borderRadius: "4px" };
    },

    getSession() {
      this.$http.get(this.userTableName + '/session', { emulateJSON: true }).then(res => {
        if (res.data && res.data.code === 0) {
          this.sessionForm = res.data.data || {};
          if (this.sessionForm.touxiang) {
            try {
              const raw = this.sessionForm.touxiang.toString();
              const fileName = raw.split('/').pop();
              if (fileName) {
                this.sessionForm.touxiang = this.baseUrl + 'file/getAvatar?fileName=' + encodeURIComponent(fileName);
              } else {
                this.sessionForm.touxiang = '';
              }
            } catch (e) {
              console.error('', e);
              this.sessionForm.touxiang = '';
            }
          }
        }
      }).catch((err) => {
        console.error('getSession ', err);
        this.$message.error('');
      });
    },

    switchTab(tabName) {
      this.activeTab = tabName;
      this.title = tabName === '' ? '' : tabName;
      if (tabName === '') { this.pageIndex = 1; this.getCancelOrderList(); }
    },

    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 
          this.$http.post(this.userTableName + '/update', this.sessionForm, { emulateJSON: true }).then(res => {
            if (res.data && res.data.code === 0) {
              this.$message.success('');
            } else {
              this.$message.error(res.data?.msg || '');
            }
          }).catch((err) => {
            console.error('', err);
            this.$message.error('');
          });
        }
      });
    },

    updatePassword() {
      this.$refs['pwdForm'].validate(valid => {
        if (!valid) return;
        if (!this.sessionForm.mima) { this.$message.error(''); return; }
        if (this.pwdForm.oldPassword !== this.sessionForm.mima) { this.$message.error(''); return; }
        const payload = { id: this.sessionForm.id, mima: this.pwdForm.newPassword };
        this.$http.post(this.userTableName + '/update', payload, { emulateJSON: true }).then(res => {
          if (res.data && res.data.code === 0) {
            this.$message.success('');
            this.pwdForm = { oldPassword: '', newPassword: '', confirmPassword: '' };
          } else {
            this.$message.error(res.data?.msg || '');
          }
        }).catch(err => {
          console.error('', err);
          const status = err && err.status ? err.status : (err && err.response && err.response.status ? err.response.status : null);
          if (status === 415) {
            this.$message.error(' (415) POST JSON');
          } else {
            this.$message.error('');
          }
        });
      });
    },

    avatarClickTip() {
      this.$message.info('');
    },

    // 
    triggerAvatarUpload() {
      const uploader = this.$refs.avatarUploader;
      if (!uploader) {
        this.$message.error('');
        return;
      }

      //  el-upload  input[type=file]
      // Element UI  input  DOM  $el 
      const el = uploader.$el || (uploader.$refs && uploader.$refs.input && uploader.$refs.input.$el) || null;
      const fileInput = (el && el.querySelector) ? el.querySelector('input[type="file"]') : null;

      if (!fileInput) {
        this.$message.error('');
        return;
      }

      //  onchange click  
      fileInput.value = '';
      fileInput.onchange = (e) => {
        const file = e.target.files && e.target.files[0];
        if (file) {
          // 
          this.uploadAvatarFile(file);
        }
        // 
        fileInput.onchange = null;
      };
      fileInput.click();
    },

    //  UI 
    uploadAvatarFile(file) {
      const formData = new FormData();
      formData.append('file', file);
      const uploadUrl = this.baseUrl + 'file/upload';
      this.uploading = true;
      this.$http.post(uploadUrl, formData, { headers: { 'Content-Type': 'multipart/form-data', Token: localStorage.getItem('Token') } }).then(res => {
        this.uploading = false;
        if (res.data && res.data.code === 0) {
          const filePath = res.data.file || (res.data.data && (res.data.data.file || res.data.data)) || res.data.url;
          if (!filePath) { this.$message.error(''); return; }
          const filenameOnly = filePath.toString().split('/').pop();
          const fullAvatarUrl = this.baseUrl + 'file/getAvatar?fileName=' + encodeURIComponent(filenameOnly);
          this.sessionForm.touxiang = fullAvatarUrl;
          //  emulateJSON 
          this.$http.post(this.userTableName + '/update', { id: this.sessionForm.id, touxiang: filePath }, { emulateJSON: true }).then(updateRes => {
            if (updateRes.data && updateRes.data.code === 0) {
              this.$message.success('');
              this.switchTab('');
            } else {
              this.$message.error(updateRes.data?.msg || '');
            }
          }).catch(err => {
            console.error('', err);
            this.$message.error('');
          });
        } else {
          this.$message.error(res.data?.msg || '');
        }
      }).catch(err => {
        this.uploading = false;
        console.error('', err);
        this.$message.error('');
      });
    },

    searchCancelOrder() { this.pageIndex = 1; this.getCancelOrderList(); },

    getCancelOrderList() {
      if (!this.sessionForm.xuehao) { this.$message.warning(''); this.cancelOrderLoading = false; return; }
      this.cancelOrderLoading = true;
      const params = { page: this.pageIndex, limit: this.pageSize, sort: 'id', order: 'desc', xuehao: this.sessionForm.xuehao };
      if (this.searchForm.yuyuedanhao) params.yuyuedanhao = `%${this.searchForm.yuyuedanhao}%`;
      this.$http({ url: 'quxiaoyuyue/page', method: 'get', params }).then(({ data }) => {
        this.cancelOrderLoading = false;
        if (data && data.code === 0) { this.cancelOrderList = data.data.list || []; this.totalPage = data.data.total || 0; }
        else { this.cancelOrderList = []; this.totalPage = 0; this.$message.warning(data?.msg || ''); }
      }).catch(() => {
        this.cancelOrderList = []; this.totalPage = 0; this.cancelOrderLoading = false; this.$message.error('');
      });
    },

    sizeChangeHandle(val) { this.pageSize = val; this.getCancelOrderList(); },
    currentChangeHandle(val) { this.pageIndex = val; this.getCancelOrderList(); }
  }
}
</script>

<style lang="scss" scoped>
/*  */
.el-form-item >>> .el-form-item__label {
  color: #333;
  font-weight: 500;
  text-align: right;
  line-height: 40px;
}
.el-input >>> .el-input__inner {
  border: 1px solid #ccc;
  border-radius: 6px;
  height: 40px;
  padding: 0 12px;
  transition: box-shadow .2s;
}
.el-input >>> .el-input__inner:focus {
  box-shadow: 0 0 6px rgba(64,158,255,0.12);
  border-color: #409EFF;
}

.el-select >>> .el-input__inner {
  border: 1px solid #ccc;
  border-radius: 6px;
  height: 40px;
  padding: 0 12px;
}

/*  */
.upload-btn {
  border-radius: 8px;
  padding: 8px 18px;
  background: linear-gradient(90deg,#4aa8ff,#2f86f6);
  color: #fff;
  box-shadow: 0 6px 18px rgba(47,134,246,0.12);
}

/*  */
.pwd-card {
  background:#fff;
  border-radius:8px;
  padding: 28px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  max-width:640px;
  margin:0 auto;
}
.pwd-form .el-form-item {
  margin-bottom: 18px;
}
.pwd-form .el-form-item .el-input__inner {
  border-radius: 6px;
  height: 44px;
  padding: 0 14px;
}

/*  */
.pwd-submit {
  background: linear-gradient(90deg,#ffb980,#ff8a65);
  border: none;
  color: #fff;
  border-radius: 24px;
  padding: 10px 22px;
  box-shadow: 0 6px 18px rgba(255,138,101,0.12);
}

/*  */
.custom-table {
  .el-table__body-wrapper tr:hover td {
    background: rgba(147, 199, 179, 0.05);
  }
}
</style>