<template>
  <div>
    <div class="container" :style="{
        minHeight: '100vh',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        background: '#f3f7fa',
        width: '100%'
      }">
      <el-form class="rgs-form" v-if="pageFlag === 'register'"
               :style="{
                 padding: '20px',
                 boxShadow: '0px 4px 10px 0px rgba(0,0,0,0.302)',
                 borderRadius: '10px',
                 background: '#fff',
                 width: '420px',
                 height: 'auto'
               }"
               ref="registerForm"
               :model="registerForm"
               :rules="rules"
               label-position="top"
      >
        <!--  -->
        <div :style="{
            width: '100%',
            margin: '0 0 16px 0',
            lineHeight: '44px',
            fontSize: '20px',
            color: '#000',
            textAlign: 'center',
            fontWeight: '700'
          }"> </div>

        <!--  -->
        <el-form-item prop="xuehao" label="">
          <el-input v-model="registerForm.xuehao" placeholder=""></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item prop="mima" label="">
          <el-input v-model="registerForm.mima" type="password" placeholder=""></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item prop="mima2" label="">
          <el-input v-model="registerForm.mima2" type="password" placeholder=""></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item prop="xingming" label="">
          <el-input v-model="registerForm.xingming" placeholder=""></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item prop="xingbie" label="">
          <el-select v-model="registerForm.xingbie" placeholder="">
            <el-option v-for="(item, index) in xueshengxingbieOptions" :key="index" :label="item" :value="item" />
          </el-select>
        </el-form-item>

        <!--  file-upload  -->
        <el-form-item prop="touxiang" label="">
          <file-upload
              tip=""
              action="file/upload"
              :limit="1"
              :multiple="false"
              :fileUrls="registerForm.touxiang ? registerForm.touxiang : ''"
              @change="xueshengtouxiangUploadChange"
          ></file-upload>
        </el-form-item>

        <!--  -->
        <el-form-item prop="shouji" label="">
          <el-input v-model="registerForm.shouji" placeholder=""></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item prop="banji" label="">
          <el-input v-model="registerForm.banji" placeholder=""></el-input>
        </el-form-item>

        <!--  -->
        <div style="width:100%; display:flex; flex-direction:column; align-items:center; gap:12px; margin-top:12px;">
          <el-button type="primary" :style="submitBtnStyle" @click="submitForm('registerForm')"></el-button>
          <el-button type="default" :style="resetBtnStyle" @click="resetForm('registerForm')"></el-button>
          <router-link :to="{ path: '/login' }" style="color:#999; margin-top:8px; display:block;"></router-link>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pageFlag: '',
      tableName: '',
      registerForm: {
        xuehao: '',
        mima: '',
        mima2: '',
        xingming: '',
        xingbie: '',
        touxiang: '',
        shouji: '',
        banji: ''
      },
      xueshengxingbieOptions: ['', ''],
      rules: {
        xuehao: [
          { required: true, message: '', trigger: 'blur' }
        ],
        mima: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        mima2: [
          { required: true, message: '', trigger: 'blur' },
          { validator: (rule, value, callback) => {
              if (value !== this.registerForm.mima) {
                callback(new Error(''));
              } else {
                callback();
              }
            }, trigger: 'blur'
          }
        ],
        xingming: [
          { required: true, message: '', trigger: 'blur' }
        ],
        shouji: [
          { required: true, validator: (rule, value, callback) => {
              if (!value) return callback(new Error(''));
              const mobileReg = /^1[3-9]\d{9}$/;
              if (!mobileReg.test(value)) return callback(new Error(''));
              callback();
            }, trigger: 'blur' }
        ]
      },
      submitBtnStyle: {
        border: '0',
        cursor: 'pointer',
        padding: '0 10px',
        outline: 'none',
        color: '#fff',
        borderRadius: '8px',
        background: '#78ABC3',
        display: 'block',
        width: '80%',
        fontSize: '16px',
        height: '44px'
      },
      resetBtnStyle: {
        border: '0',
        cursor: 'pointer',
        padding: '0 10px',
        outline: 'none',
        color: '#fff',
        borderRadius: '8px',
        background: '#B0C9CF',
        display: 'block',
        width: '80%',
        fontSize: '16px',
        height: '44px'
      }
    };
  },
  created() {
    this.pageFlag = this.$route.query.pageFlag || 'register';
    this.tableName = this.$route.query.role || 'xuesheng';
    // keep rules in data; already defined
  },
  methods: {
    getUUID() {
      return new Date().getTime();
    },
    xueshengtouxiangUploadChange(fileUrls) {
      // file-upload  URL baseUrl
      if (!fileUrls) {
        this.registerForm.touxiang = '';
        return;
      }
      this.registerForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl, 'g'), '');
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 
          if (this.registerForm.mima !== this.registerForm.mima2) {
            this.$message.error('');
            return;
          }

          const url = `${this.tableName}/register`;

          //  emulateJSON:true  application/x-www-form-urlencoded  415
          this.$http.post(url, this.registerForm, { emulateJSON: true })
              .then(res => {
                if (res.data && res.data.code === 0) {
                  this.$message({
                    message: '',
                    type: 'success',
                    duration: 1500,
                    onClose: () => {
                      this.$router.push('/login');
                    }
                  });
                } else {
                  //  401/415/ msg
                  this.$message.error(res.data?.msg || '');
                }
              })
              .catch(err => {
                //  415 
                console.error('', err);
                const status = (err && err.status) || (err && err.response && err.response.status) || null;
                if (status === 415) {
                  this.$message.error(' (415)register POST ');
                } else if (status === 401) {
                  this.$message.error('401');
                } else {
                  this.$message.error('');
                }
              });
        } else {
          // 
          return false;
        }
      });
    },
    resetForm(formName) {
      if (this.$refs[formName]) this.$refs[formName].resetFields();
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.container {
  .el-date-editor.el-input {
    width: 100%;
  }

  .rgs-form .el-input >>> .el-input__inner {
    border: 0;
    border-radius: 8px;
    padding: 0 10px;
    outline: none;
    color: #000;
    background: #F9F9F9;
    width: 100%;
    font-size: 14px;
    height: 44px;
  }

  .rgs-form .el-select >>> .el-input__inner {
    border: 0;
    border-radius: 8px;
    padding: 0 10px;
    outline: none;
    color: #000;
    background: #F9F9F9;
    width: 100%;
    font-size: 14px;
    height: 44px;
  }

  .rgs-form .el-date-editor >>> .el-input__inner {
    border: 0;
    border-radius: 8px;
    padding: 0 10px 0 30px;
    outline: none;
    color: #000;
    background: #F9F9F9;
    width: 100%;
    font-size: 14px;
    height: 44px;
  }

  .rgs-form >>> .el-upload--picture-card {
    background: transparent;
    border: 0;
    border-radius: 0;
    width: auto;
    height: auto;
    line-height: initial;
    vertical-align: middle;
  }

  /* required Element UI  el-form-item  is-required  */
  .el-form-item.is-required .el-form-item__label::after {
    content: " *";
    color: #f56c6c;
    margin-left: 4px;
  }

  .rgs-form >>> .upload .upload-img {
    border: 1px dashed #888;
    cursor: pointer;
    border-radius: 8px;
    color: #888;
    width: 100px;
    font-size: 32px;
    line-height: 100px;
    text-align: center;
    height: 100px;
  }

  .rgs-form >>> .el-upload-list .el-upload-list__item {
    border: 1px dashed #888;
    cursor: pointer;
    border-radius: 8px;
    color: #888;
    width: 100px;
    font-size: 32px;
    line-height: 100px;
    text-align: center;
    height: 100px;
  }

  .rgs-form >>> .el-upload .el-icon-plus {
    border: 1px dashed #888;
    cursor: pointer;
    border-radius: 8px;
    color: #888;
    width: 100px;
    font-size: 32px;
    line-height: 100px;
    text-align: center;
    height: 100px;
  }
}
</style>