<template>
  <div class="addEdit-block" style="width: 100%">
    <!--  -->
    <div :style='{"width":"100%","padding":"20px","margin":"10px auto","borderRadius":"27px","background":"#bfd2fc","boxSizing":"border-box"}' class="breadcrumb-preview">
      <el-breadcrumb :separator="''" :style='{"fontSize":"14px","lineHeight":"1"}'>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <el-form
        :style="{
        padding: '30px',
        boxShadow: '0 0px 0px rgba(64, 158, 255, .3)',
        borderRadius: '6px',
        flexWrap: 'wrap',
        background: '#fff',
        display: 'flex',
        width: '100%',
        maxWidth: '1200px',
        margin: '20px auto'
      }"
        class="add-update-preview"
        ref="ruleForm"
        :model="ruleForm"
        :rules="rules"
        label-width="100px"
    >
      <template>
        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="yuyuedanhao"
        >
          <el-input
              v-model="ruleForm.yuyuedanhao"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="mingcheng"
        >
          <el-input
              v-model="ruleForm.mingcheng"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="zuowei"
        >
          <el-input
              v-model="ruleForm.zuowei"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="date"
            label=""
            prop="quxiaoshijian"
        >
          <el-date-picker
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="ruleForm.quxiaoshijian"
              type="datetime"
              placeholder=""
              readonly
          ></el-date-picker>
        </el-form-item>

        <!-- + -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="date"
            label=""
            prop="yuyue_start"
        >
          <el-date-picker
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="ruleForm.yuyue_start"
              type="datetime"
              placeholder=""
              readonly
          ></el-date-picker>
        </el-form-item>

        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="date"
            label=""
            prop="yuyue_end"
        >
          <el-date-picker
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="ruleForm.yuyue_end"
              type="datetime"
              placeholder=""
              readonly
          ></el-date-picker>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="xuehao"
        >
          <el-input
              v-model="ruleForm.xuehao"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="xingming"
        >
          <el-input
              v-model="ruleForm.xingming"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="shouji"
        >
          <el-input
              v-model="ruleForm.shouji"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '100%', margin: '0 0 20px 0' }"
            class="textarea"
            label=""
            prop="quxiaoyuanyin"
        >
          <el-input
              style="width: 100%; max-width: 800px"
              type="textarea"
              :rows="5"
              placeholder=""
              v-model="ruleForm.quxiaoyuanyin"
              :disabled="!canCancel(ruleForm)"
          >
          </el-input>
          <div v-if="!canCancel(ruleForm)" style="margin-top:8px;color:#f56c6c">
            {{ cancelReason(ruleForm) }}
          </div>
        </el-form-item>
      </template>

      <!--  -->
      <el-form-item
          :style="{ width: '100%', padding: '0', margin: '0', textAlign: 'center' }"
          class="btn"
      >
        <el-button
            :style="{
            border: '0',
            cursor: canCancel(ruleForm) ? 'pointer' : 'not-allowed',
            padding: '0',
            margin: '0 20px 0 0',
            outline: 'none',
            color: 'rgba(255, 255, 255, 1)',
            borderRadius: '30px',
            background: '#93C7B3',
            width: '128px',
            lineHeight: '40px',
            fontSize: '14px',
            height: '40px',
          }"
            type="primary"
            class="btn-success"
            @click="onSubmit"
            :disabled="!canCancel(ruleForm)"
        ></el-button
        >
        <el-button
            :style="{
            border: '0px solid rgba(64, 158, 255, 1)',
            cursor: 'pointer',
            padding: '0',
            margin: '0',
            outline: 'none',
            color: '#fff',
            borderRadius: '30px',
            background: '#93C7B3',
            width: '128px',
            lineHeight: '40px',
            fontSize: '14px',
            height: '40px',
          }"
            class="btn-close"
            @click="back()"
        ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
// url
import { isMobile } from "@/utils/validate";
export default {
  data() {
    let self = this;
    var validateMobile = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isMobile(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      baseUrl: '',
      ruleForm: {
        yuyuedanhao: "",
        mingcheng: "",
        zuowei: "",
        yuyue_start: "", //  yyyy-MM-dd HH:mm:ss
        yuyue_end: "",   // 
        quxiaoshijian: "",
        quxiaoyuanyin: "",
        xuehao: "",
        xingming: "",
        shouji: "",
        yuyueId: "", // ID
        zixishiid: "", // ID
        qiandaozhuangtai: "", // 
        qiantuizhuangtai: "", // 
        weiguiFlag: null //  weiguiFlag  weigui_flag
      },
      rules: {
        quxiaoyuanyin: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 200, message: '5-200', trigger: 'blur' }
        ],
        shouji: [{ validator: validateMobile, trigger: "blur" }]
      }
    };
  },
  created() {
    this.baseUrl = this.$config.baseUrl;
    // 
    this.ruleForm.quxiaoshijian = this.getCurDateTime();
    // list.vue
    this.initOrderData();
  },
  methods: {
    // yyyy-MM-dd HH:mm:ss
    getCurDateTime() {
      const date = new Date();
      const y = date.getFullYear();
      const m = (date.getMonth() + 1).toString().padStart(2, '0');
      const d = date.getDate().toString().padStart(2, '0');
      const h = date.getHours().toString().padStart(2, '0');
      const min = date.getMinutes().toString().padStart(2, '0');
      const s = date.getSeconds().toString().padStart(2, '0');
      return `${y}-${m}-${d} ${h}:${min}:${s}`;
    },

    // 
    initOrderData() {
      console.log(':', this.$route.query);

      let order = null;

      // orderData
      if (this.$route.query.orderData) {
        try {
          order = JSON.parse(this.$route.query.orderData);
          console.log(':', order);
        } catch (e) {
          console.error('orderData:', e);
          this.$message.error('');
          this.back();
          return;
        }
      }

      // 
      if (!order) {
        this.$message.error('');
        this.back();
        return;
      }

      // 
      if (!order.id) {
        this.$message.error('ID');
        this.back();
        return;
      }
      if (!order.zixishiid) {
        this.$message.error('ID');
        this.back();
        return;
      }
      if (!order.zuowei && order.zuowei !== 0) {
        this.$message.error('');
        this.back();
        return;
      }

      // 
      this.ruleForm.yuyuedanhao = order.yuyuedanhao || '';
      this.ruleForm.mingcheng = order.mingcheng || '';
      this.ruleForm.zuowei = order.zuowei || '';
      this.ruleForm.yuyue_start = order.yuyueStart || order.yuyue_start || '';
      this.ruleForm.yuyue_end = order.yuyueEnd || order.yuyue_end || '';
      this.ruleForm.xuehao = order.xuehao || '';
      this.ruleForm.xingming = order.xingming || '';
      this.ruleForm.shouji = order.shouji || '';
      this.ruleForm.yuyueId = order.id || '';
      this.ruleForm.zixishiid = order.zixishiid || '';
      this.ruleForm.qiandaozhuangtai = order.qiandaozhuangtai || '';
      this.ruleForm.qiantuizhuangtai = order.qiantuizhuangtai || '';
      // 
      this.ruleForm.weiguiFlag = order.weiguiFlag != null ? order.weiguiFlag : order.weigui_flag != null ? order.weigui_flag : null;

      // 
      if (!this.canCancel(this.ruleForm)) {
        // 
        this.$message.warning(this.cancelReason(this.ruleForm));
      }

      console.log(':', this.ruleForm);
    },

    // 
    onSubmit() {
      // 
      if (!this.canCancel(this.ruleForm)) {
        this.$message.warning(this.cancelReason(this.ruleForm));
        return;
      }

      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          // 
          const submitData = {
            crossrefid: this.ruleForm.yuyueId,
            yuyueId: this.ruleForm.yuyueId,
            yuyuedanhao: this.ruleForm.yuyuedanhao,
            mingcheng: this.ruleForm.mingcheng,
            zuowei: this.ruleForm.zuowei,
            yuyue_start: this.ruleForm.yuyue_start,
            yuyue_end: this.ruleForm.yuyue_end,
            quxiaoshijian: this.ruleForm.quxiaoshijian,
            quxiaoyuanyin: this.ruleForm.quxiaoyuanyin,
            xuehao: this.ruleForm.xuehao,
            xingming: this.ruleForm.xingming,
            shouji: this.ruleForm.shouji,
            zixishiid: this.ruleForm.zixishiid
          };

          console.log(':', submitData);

          this.$http.post('quxiaoyuyue/save', submitData).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.$router.push('/index/yuyuexinxi');
                }
              });
            } else {
              // 
              this.$message.error(data.msg || '');
            }
          }).catch((error) => {
            console.error(':', error);
            if (error.status === 500) {
              this.$message.error('');
            } else if (error.status === 404) {
              this.$message.error('');
            } else {
              this.$message.error('');
            }
          });
        } else {
          this.$message.error('5-200');
        }
      });
    },

    // 
    back() {
      this.$router.push('/index/yuyuexinxi');
    },

    //  "yyyy-MM-dd HH:mm:ss"  Date
    parseDateTimeString(dateTimeStr) {
      if (!dateTimeStr || typeof dateTimeStr !== 'string') return null;
      const parts = dateTimeStr.trim().split(' ');
      if (parts.length < 2) return null;
      const datePart = parts[0];
      const timePart = parts[1];
      const [y, m, d] = datePart.split('-').map(Number);
      const [hh, mm, ss] = (timePart || '00:00:00').split(':').map(Number);
      if (![y, m, d].every(n => !isNaN(n))) return null;
      return new Date(y, (m || 1) - 1, d || 1, hh || 0, mm || 0, ss || 0);
    },

    // 
    canCancel(row) {
      if (!row) return false;
      // 
      const wf = row.weiguiFlag != null ? row.weiguiFlag : row.weigui_flag != null ? row.weigui_flag : null;
      if (wf === 1 || String(wf) === '1') return false;

      //    -> 
      if (row.qiandaozhuangtai === '' && row.qiantuizhuangtai !== '') return false;

      //  < 
      const start = row.yuyue_start || row.yuyueStart || row.yuyueStart;
      if (!start) return false;
      const startDate = this.parseDateTimeString(start);
      if (!startDate) return false;

      return new Date().getTime() < startDate.getTime();
    },

    // 
    cancelReason(row) {
      if (!row) return '';
      const wf = row.weiguiFlag != null ? row.weiguiFlag : row.weigui_flag != null ? row.weigui_flag : null;
      if (wf === 1 || String(wf) === '1') return '';
      if (row.qiandaozhuangtai === '' && row.qiantuizhuangtai !== '') return '';
      const start = row.yuyue_start || row.yuyueStart || row.yuyueStart;
      if (start) {
        const startDate = this.parseDateTimeString(start);
        if (startDate && new Date().getTime() >= startDate.getTime()) return '';
      }
      return '';
    }
  }
};
</script>
<style lang="scss" scoped>
.amap-wrapper {
  width: 100%;
  height: 500px;
}

.search-box {
  position: absolute;
}

